.App {
  background: -owg-linear-gradient(120deg, rgba(192, 192, 192, 0.3), rgba(128, 128, 128, 0.6), #dddddd);
  background: -webkit-linear-gradient(120deg, rgba(192, 192, 192, 0.3), rgba(128, 128, 128, 0.6), #dddddd);
  background: -moz-linear-gradient(120deg, rgba(192, 192, 192, 0.3), rgba(128, 128, 128, 0.6), #dddddd);
  background: -o-linear-gradient(120deg, rgba(192, 192, 192, 0.3), rgba(128, 128, 128, 0.6), #dddddd);
  background: linear-gradient(120deg, rgba(192, 192, 192, 0.3), rgba(128, 128, 128, 0.6), #dddddd); }

.App-logo {
  height: 50px;
  margin: 10px;
  display: inline;
  vertical-align: middle; }

li {
  list-style: none; }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }



